/**
 * 查找url参数中指定name的参数
 * @param {*} name
 */
function getQuery(name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
  var r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return unescape(r[2]);
  }
  return null;
}

/**
 * 查找url参数中指定name的参数 多个问号
 * @param {*} name
 */
function getQueryNew(name) {
  var reg = new RegExp(name + '=([^&]*)');
  //
  // var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  var index = window.location.href.indexOf('?');
  var r = decodeURIComponent(window.location.href).substr(index, window.location.href.length).match(reg);
  // alert(decodeURIComponent(window.location.href).substr(index, window.location.href.length))
  // alert(r)
  if (r != null) {
    // return unescape(r[2])
    return unescape(r[1]);
  }
  return null;
}

/**
 * 优惠券解密
 * @param 加密参数
 */
function xDeCode(param) {
  if (param) {
    // 进行解密
    var Separator = 'lifaair';
    var str = Base64.decode(param);
    var finStr = str.replace(new RegExp(Separator, 'g'), ",");
    // 去除最后一位 ,
    finStr = finStr.substr(0, finStr.length - 1);
    return finStr;
  }
  return null;
}

